.btn {
  min-width: 100px;
  height: 40px;
  border-radius: 10px;
  border: 0;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.19));
  font-size: 18px;
  cursor: pointer;
  padding: 0 10px;

  &.solid {
    color: white;
  }

  &.outlined {
    background: white;
  }

  &:hover {
    transform: scale(1.05);
  }
}
